.m-listCtrl{position: fixed;left: 0;top: 0;right: 0;bottom: 0;z-index: 998;overflow: hidden;-webkit-transition-property: -webkit-transform;transition-property: -webkit-transform;-o-transition-property: transform;transition-property: transform;transition-property: transform, -webkit-transform;}

.m-listCtrl_bg, .m-listCtrl_box{position: absolute;left: 0;right: 0;}

.m-listCtrl_bg{top: 0;bottom: 0;z-index: 1;background: #000;-webkit-transition-property: opacity;-o-transition-property: opacity;transition-property: opacity;}

.m-listCtrl_box{bottom: 0;z-index: 2;background: #fff;border-top-left-radius: .5rem;border-top-right-radius: .5rem;overflow: hidden;-webkit-transition-property: opacity, -webkit-transform;transition-property: opacity, -webkit-transform;-o-transition-property: transform, opacity;transition-property: transform, opacity;transition-property: transform, opacity, -webkit-transform;}

.m-listCtrl.m-listCtrl_show{-webkit-transition-duration: 0;-o-transition-duration: 0;transition-duration: 0;-webkit-transform: translate3d(0%,0%, 0);transform: translate3d(0%,0%, 0);}
.m-listCtrl.m-listCtrl_hide{-webkit-transition-delay: 300ms;-o-transition-delay: 300ms;transition-delay: 300ms;-webkit-transition-duration: 0;-o-transition-duration: 0;transition-duration: 0;-webkit-transform: translate3d(0%,100%, 0);transform: translate3d(0%,100%, 0);}

.m-listCtrl.m-listCtrl_show .m-listCtrl_bg{opacity: .75;-webkit-transition-duration: 300ms;-o-transition-duration: 300ms;transition-duration: 300ms;}
.m-listCtrl.m-listCtrl_hide .m-listCtrl_bg{opacity: 0;-webkit-transition-duration: 300ms;-o-transition-duration: 300ms;transition-duration: 300ms;}

.m-listCtrl.m-listCtrl_show .m-listCtrl_box{opacity: 1;-webkit-transition-duration: 300ms;-o-transition-duration: 300ms;transition-duration: 300ms;-webkit-transform: translate3d(0%,0%, 0);transform: translate3d(0%,0%, 0);}
.m-listCtrl.m-listCtrl_hide .m-listCtrl_box{opacity: 0;-webkit-transition-duration: 300ms;-o-transition-duration: 300ms;transition-duration: 300ms;-webkit-transform: translate3d(0%,100%, 0);transform: translate3d(0%,100%, 0);}

.m-listCtrl_box .t{display: -ms-flexbox;display: flex;padding: 0 .5rem;line-height: 2.2rem;}
    .m-listCtrl_box .t p{-ms-flex: 1 1;flex: 1 1;font-size: .8rem;font-weight: bold;}
    .m-listCtrl_box .t p span{color: #98999A;font-size: .7rem;}
    .m-listCtrl_box .t .r{width: 2rem;text-align: center;color: #98999A;}

.m-listCtrl_box ul{max-height: 15rem;overflow: auto;}
    .m-listCtrl_box ul li{display: -ms-flexbox;display: flex;padding: 0 .5rem;line-height: 2rem;}
    .m-listCtrl_box ul li .l{-ms-flex: 1 1;flex: 1 1;overflow: hidden;}
    .m-listCtrl_box ul li .l span{font-size: .6rem;color: #98999A;}
    .m-listCtrl_box ul li .r{width: 2rem;text-align: center;color: #98999A;}
    .m-listCtrl_box ul li .r .iconfont{font-size: .6rem;}
    .m-listCtrl_box ul li.active .l{color: #E4463B;}



