.m-botCtrl{display: -ms-flexbox;display: flex;height: 2.5rem;background: #fff;}
.m-botCtrl .l{-ms-flex: 1 1;flex: 1 1;display: -ms-flexbox;display: flex;overflow: hidden;}
    .m-botCtrl .l img{width: 2rem;border-radius: .2rem;margin: .25rem;}
    .m-botCtrl .l .i{-ms-flex: 1 1;flex: 1 1;padding: .25rem 0;overflow: hidden;}
    .m-botCtrl .l .i p{line-height: 1rem;height: 1rem;}
    .m-botCtrl .l .i span{font-size: .6rem;color: #98999A;}
.m-botCtrl .r{width: 5rem;display: -ms-flexbox;display: flex;}
    .m-botCtrl .r em{-ms-flex: 1 1;flex: 1 1;line-height: 2.5rem;text-align: center;}
    .m-botCtrl .r em .iconfont{font-size: 1.4rem;}



