.m-viewCtrl{position: fixed;left: 0;top: 0;right: 0;bottom: 0;z-index: 997;overflow: hidden;-webkit-transition-property: opacity, -webkit-transform;transition-property: opacity, -webkit-transform;-o-transition-property: transform, opacity;transition-property: transform, opacity;transition-property: transform, opacity, -webkit-transform;}
    .m-viewCtrl .bg, .m-viewCtrl .box{position: absolute;left: 0;top: 0;right: 0;bottom: 0;}
    .m-viewCtrl .bg{z-index: 1;}
    .m-viewCtrl .box{z-index: 2;}
    .m-viewCtrl .bg img{display: block;width: 120%;height: 120%;margin: -10%;-webkit-filter: blur(30px);filter: blur(30px);}
    .m-viewCtrl .bg::after{content: '';position: absolute;left: 0;top: 0;right: 0;bottom: 0;background: rgba(110, 110, 110, .5);}

.m-viewCtrl.m-viewCtrl_show{opacity: 1;-webkit-transition-duration: 300ms;-o-transition-duration: 300ms;transition-duration: 300ms;-webkit-transform: translate3d(0%,0%, 0);transform: translate3d(0%,0%, 0);}
.m-viewCtrl.m-viewCtrl_hide{opacity: 0;-webkit-transition-duration: 300ms;-o-transition-duration: 300ms;transition-duration: 300ms;-webkit-transform: translate3d(0%,100%, 0);transform: translate3d(0%,100%, 0);}

.m-viewCtrl_header, .m-viewCtrl_warp, .m-viewCtrl_time, .m-viewCtrl_footer{position: absolute;left: 0;right: 0;}

.m-viewCtrl_header{display: -ms-flexbox;display: flex;height: 2.5rem;top: 0;}
    .m-viewCtrl_header .l{width: 3rem;text-align: center;line-height: 2.5rem;color: #fff;}
    .m-viewCtrl_header .l .iconfont{font-size: 1.2rem;}
    .m-viewCtrl_header .r{-ms-flex: 1 1;flex: 1 1;padding: .25rem 0;overflow: hidden;}
    .m-viewCtrl_header .r p{color: #fff;font-size: .8rem;line-height: 1rem;}
    .m-viewCtrl_header .r span{font-size: .6rem;color: #D1CEBF;}

.m-viewCtrl_warp{top: 2.5rem;bottom: 6rem;}

.m-viewCtrl_disc{width: 12rem;height: 12rem;margin: 3.5rem auto 0 auto;position: relative;}
    .m-viewCtrl_disc::before, .m-viewCtrl_disc .pic, .m-viewCtrl_disc .ani{position: absolute;left: 0;top: 0;}
    .m-viewCtrl_disc::before{content: '';width: 100%;height: 100%;background: url(../../../assets/img/disc.png) no-repeat;background-size: 100% 100%;z-index: 2;}
    .m-viewCtrl_disc .pic{width: 100%;height: 100%;z-index: 1;}
    .m-viewCtrl_disc .pic img{width: 7.6rem;height: 7.6rem;position: absolute;left: 2.2rem;top: 2.2rem;z-index: 1;}
    .m-viewCtrl_disc .ani{content: '';width: 100%;height: 100%;background: url(../../../assets/img/disc_ani.png) no-repeat;background-size: 100% 100%;z-index: 3;}
    .m-viewCtrl_disc .circling{-webkit-animation: circling 20s infinite linear;animation: circling 20s infinite linear;}
@-webkit-keyframes circling{
    0%{-webkit-transform:rotate(0deg);transform:rotate(0deg);}
    to{-webkit-transform:rotate(1turn);transform:rotate(1turn);}
}
@keyframes circling{
    0%{-webkit-transform:rotate(0deg);transform:rotate(0deg);}
    to{-webkit-transform:rotate(1turn);transform:rotate(1turn);}
}

.m-viewCtrl_lyric{position: absolute;left: 0;top: 0;right: 0;bottom: 0;overflow-x: hidden;overflow-y: auto;-webkit-overflow-scrolling:touch;}
    .m-viewCtrl_lyric ul{position: absolute;left: 0;right: 0;top: 41%;-webkit-transition: -webkit-transform .3s ease-out;transition: -webkit-transform .3s ease-out;-o-transition: transform .3s ease-out;transition: transform .3s ease-out;transition: transform .3s ease-out, -webkit-transform .3s ease-out;-webkit-transform-origin: 0 0 0;-ms-transform-origin: 0 0 0;transform-origin: 0 0 0;}
    .m-viewCtrl_lyric ul li{text-align: center;line-height: 32px;color: #D1CEBF;}
    .m-viewCtrl_lyric ul .active{color: #fff;}

.m-viewCtrl_time{display: -ms-flexbox;display: flex;height: 2rem;bottom: 4rem;}
    .m-viewCtrl_time .l, .m-viewCtrl_time .r{width: 3.4rem;font-size: .6rem;line-height: 2rem;}
    .m-viewCtrl_time .l{padding-right: .7rem;text-align: right;color: #F0EDDE;}
    .m-viewCtrl_time .r{padding-left: .7rem;color: #D1CEBF;}
    .m-viewCtrl_time .c{-ms-flex: 1 1;flex: 1 1;position: relative;}
    .m-viewCtrl_time .c .line-box{height: .1rem;border-radius: .1rem;position: absolute;left: 0;top: .95rem;right: 0;}
    .m-viewCtrl_time .c .line{position: absolute;left: 0;top: 0;bottom: 0;}
    .m-viewCtrl_time .c .line1{right: 0;z-index: 1;background: #D1CEBF;opacity: .5;}
    .m-viewCtrl_time .c .line2{z-index: 2;background: #F0EDDE;opacity: .5;}
    .m-viewCtrl_time .c .line3{z-index: 3;background: #D53931;}
    .m-viewCtrl_time .c .line3::after{content: '';width: .1rem;height: .1rem;background: #D53931;border-radius: 50%;border: .3rem solid #fff;position: absolute;right: -.6rem;top: -.3rem;}

.m-viewCtrl_footer{display: -ms-flexbox;display: flex;height: 4rem;bottom: 0;background: -webkit-gradient(linear, left bottom, left top, from(rgba(0, 0, 0, .2)), color-stop(60%, rgba(0, 0, 0, .1)), to(rgba(0, 0, 0, 0)));background: -webkit-linear-gradient(bottom, rgba(0, 0, 0, .2), rgba(0, 0, 0, .1) 60%, rgba(0, 0, 0, 0));background: -o-linear-gradient(bottom, rgba(0, 0, 0, .2), rgba(0, 0, 0, .1) 60%, rgba(0, 0, 0, 0));background: linear-gradient(0deg, rgba(0, 0, 0, .2), rgba(0, 0, 0, .1) 60%, rgba(0, 0, 0, 0));}
    .m-viewCtrl_footer .l, .m-viewCtrl_footer .c{line-height: 4rem;}
    .m-viewCtrl_footer .l{width: 3.8rem;text-align: center;}
    .m-viewCtrl_footer .c{display: -ms-flexbox;display: flex;-ms-flex: 1 1;flex: 1 1;}
    .m-viewCtrl_footer .c em{-ms-flex: 1 1;flex: 1 1;text-align: center;}
    .m-viewCtrl_footer .l .iconfont{font-size: 1.6rem;color: #C1BFAA;}
    .m-viewCtrl_footer .c .iconfont{font-size: 1.8rem;color: #E8E8DE;}
    .m-viewCtrl_footer .c em:nth-child(2) .iconfont{font-size: 2.2rem;}



